import footerSubmit from '@/components/footerSubmit/footerSubmit.vue';
import { balancePayment, wechatPayment, getWxSettings } from '@/api/index.js';
export default {
  name: 'Payment',
  data() {
    return {
      currentChoosedIndex: 0,
      paymentMethods: [{
        name: '微信支付',
        icon: require('../../assets/wx.png')
      }]
    };
  },
  methods: {
    //确认支付
    handleSubmit() {
      if (this.currentChoosedIndex == 1) {
        this.balancePayment();
      }else{
        this.wechatPayment();
      }
    },
    //余额支付
    balancePayment() {
      balancePayment({
        order_no: this.$store.state.payment.orderNo,
        user_id: this.$store.state.user.userid,
        order_type: this.$store.state.payment.orderType
      }).then(res => {
        if (res.data.code == 1) {
          this.feedback.Toast({
            msg: res.data.info,
            timeout: 1000
          });
          // setTimeout(() => {
          //   var str = ''
          //   if (that.data.kind == 1) {
          //     str = '../order/order?index=1'
          //   } else {
          //     str = '../takeorder/takeorder';
          //     wx.switchTab({
          //       url: str
          //     });
          //     return;
          //   }
          // }, 1000)
        } else {
          this.feedback.Toast({
            msg: res.data.info,
            timeout: 1000
          });
        }
      });
    },
    //微信支付
    wechatPayment() {
      wechatPayment({
        order_no: this.$store.state.payment.orderNo,
        user_id: this.$store.state.user.userid,
        order_type: this.$store.state.payment.orderType,
        type: 2
      }).then(res=>{
        this.wx.chooseWXPay({
          timestamp: res.data.data.timestamp.toString(),
          nonceStr: res.data.data.nonceStr,
          package: res.data.data.package,
          signType: res.data.data.signType,
          paySign: res.data.data.paySign,
          success: res=> {
            this.feedback.Alert({
              msg:'付款成功，现在为你跳转订单页面',
              callback:()=>{
                // if (that.data.kind == 1) {
                //   str = '../order/order?index=1'
                // } else if (that.data.kind == 3) {
                //   str = '../order/order?index=1'
                // } else {
                //   str = '../takeorder/takeorder';
                //   wx.switchTab({
                //       url: str
                //   });
                //   return;
                // }
              }
            })
          },
          fail:err=>{
            this.feedback.Toast({
              msg:JSON.stringify(err),
              timeout:20000
            })
          }
        });
      })
    }
  },
  mounted() {
    if(!this.$store.state.payment.orderNo){
      this.feedback.Notify({
        msg:'订单信息有误,请重新选择支付',
        timeout:1800,
        callback:()=>{
          this.$router.go(-1)
        }
      })
      return;
    }
    if (this.$store.state.payment.canUse) {
      this.paymentMethods.push({
        name: '余额支付',
        icon: require('../../assets/ye.png'),
      })
    }
    getWxSettings().then(res => {
      let data = res.data.data;
      this.wx.config({
        debug: global.isDev,
        appId: data.appid,
        timestamp: data.timestamp,
        nonceStr: data.nonceStr,
        signature: data.signature,
        jsApiList: ['chooseWXPay']
      });
    });
  },
  components: {
    footerSubmit
  }
};




